import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import EditRatedDescription from "../../organisms/EditRatedDescription";
import { updateSkills, selectSkills } from "./skillsSlice";

export default function SkillsFeature(props) {
  const skills = useSelector(selectSkills);
  const dispatch = useDispatch();

  const [userSkills, setUserSkills] = useState(skills);

  function onClickHandler(event) {
    if (event.target.getAttribute("data-type") === "tablet") {
      const row = event.target.closest("[data-row-id]").getAttribute("data-row-id");
      const value = event.target.getAttribute("data-id");
      const description = [...userSkills.description];
      description[row].rating = value;

      setUserSkills({
        ...userSkills,
        description: [...description],
      });
    }
  }

  return (
    <div onClick={onClickHandler}>
      <EditRatedDescription title={userSkills.title} description={userSkills.description} />
    </div>
  );
}
